import React from 'react'
import {
    withRouter
} from 'react-router-dom'
import './css/shangpin.css'
import Tabsp from './Tabsp';
const Shangpin = (props) => {
    return (
        <div className='shangpin'>
            <div className='shangpin-image'>
                <img src="//img.alicdn.com/imgextra/i1/1660766192/O1CN015D6t9U1vbz3CG8hCs_!!1660766192-0-lubanu-s.jpg_760x760Q50s50.jpg_.webp" alt=""></img>
                <span className='shangpin-button-one' onClick={()=>{props.history.push('/')}}><i className='iconfont icon-fanhui'/></span>
                <Tabsp/>
            </div>
        </div>
    )
}
export default withRouter(Shangpin);