import { Grid } from 'antd-mobile';
import React from 'react'
import './css/mygrid.css'
import spshuju from './shangpin.shuju'
import {withRouter
} from 'react-router-dom'
const Mygrid = (props) => (
  <div className='mygrid'>
    <div className='mygridimg'><img src='./images/bg01.png' alt=''/></div>
    <Grid
      data={spshuju}
      columnNum={2}
      itemStyle={{ height: '260px', width: '171px', margin: '9px 4.5px 0px 4.5px', background: 'rgba(0,0,0,.05)' }}
      onClick={dataItem =>{
        props.history.push('/shangpin');
      }}
      hasLine={false}
      renderItem={dataItem => (
        <div className='dataitem'>
          <img className='dataitem-img' style={{border:'node',}} src={dataItem.url} alt="" />
          <div className='dataitem-div-one'>{dataItem.info}</div>
          <div className='dataitm-div-two'>
              <span className='dataitem-span-one'>￥</span>
              <span className='dataitem-span-two'>{dataItem.money}</span>
              <span className='dataitem-span-three'>{dataItem.many}</span>
          </div>
        </div>
      )
      }
    />
    <div className='dataitem-bottom'></div>
  </div>
);

export default withRouter(Mygrid);