import React from 'react'
import 'antd-mobile/dist/antd-mobile.css'
import { TabBar } from "antd-mobile"
import { withRouter } from "react-router-dom"

class Tabsp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        return (
            <div style={{ position: 'fixed', height: '70px', width: '100%', bottom: 0 }}>
                <TabBar
                    unselectedTintColor=""
                    tintColor="#5D656B"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont icon-dianpu'></i>
                        }
                        
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-kefu'></i>
                        }
                        title="客服"
                        key="kefu"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        title="收藏"
                        key="shoucang"
                    ></TabBar.Item>
                    <TabBar.Item
                        title="加入购物车"
                        key="jiaru"
                    ></TabBar.Item>
                    <TabBar.Item
                        title="立即购买"
                        key="liji"
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}

export default withRouter(Tabsp)
